
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'FileLogin',
components: {
XHeader
},
data () {
return {
type: "", // 来源类型
visible: false,
pwd:'',
disable: true,
errorTips: false
}

},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.type = routerParams.TYPE;
},

cancelGLDAFunc() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "wdjt",
"MSH.2": "cancelGLDA",
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
"CJKDAMM": this.pwd,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();

let len = history.length;
history.go(-(len-1));
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
changePwdStatus(){
this.visible = !this.visible;
},
signIn(){
if(!this.disable){
if (this.type == "取消关联") {
this.cancelGLDAFunc();
} else {
this.$ajax.post('',{
'MSH.1':'healthFileBasicInfoService',
'MSH.2':'checkPassword',
'token':this.$root.loginInfo.token,
'password':this.pwd,
'token':this.$root.loginInfo.token,
}).then(data => {
if(typeof data !='undefined'){
if(data.status==1){
this.$vux.toast.text("没有设置健康档案密码 ", "bottom");
}else if(data.status==2){
this.$vux.toast.text("密码错误", "bottom");
}else if(data.status==3){
this.$router.push('HealthRrecords');
}
}
}).catch(error => {
this.errorTips = true;
this.pwd = '';
console.log(error);
});
}
}
}
},
watch: {
pwd(val){
if(val.length == 1 ){
this.errorTips = false;
}

if(val.length == 6){
this.disable = false;
return;
}
this.disable = true;
}
}
}



